.user {
    display: flex;
  }
  .user_icon {
    /* padding: 16px; */
    height: 40px;
    width: 40px;
    background-color: blue;
    display: grid;
    place-items: center;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    text-transform: uppercase;
    color: white;
    font-weight: 700;
  }
  .dot {
    position: relative;
    top: 25px;
    right: 15px;
    color: grey;
  }
  